#footer {
  position: relative;
  border-top: 4px solid #333;
  color: #fff;
  opacity: 0.5;
  text-align: center;
  padding: 50px 0;
  font-family: "Roboto", sans-serif;
  z-index: 1;
}

@media screen and (max-width: 900px) {
  #footer {
    font-size: 2.5vw;
    padding: 30px 0;
  }
}
